import {FETCH_ACCESS_TOKEN} from './actionType';

const initialState = '';

export default function accessToken(state = initialState, actions ={}) {
	switch (actions.type) {
		case FETCH_ACCESS_TOKEN:
			return actions.payload;
		default:
			return state;
	}
}

export const getAccessToken = (state) => {
	return state.accessToken;
}