import React, {useEffect} from 'react';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';

import * as rootSelects from '../../store/reducers';
import * as rootActions from '../../store/actions';

import {Panel, PanelHeader, HeaderButton, platform, IOS} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

import User from '../../components/User';

const useFetchedFriends = (dispatch) => {
	useEffect(() => {
		const {
			frendsActions: {fetchedFriends}
		} = rootActions;

		dispatch(fetchedFriends());
	}, []);
};

const FriendsContainer = (props) => {
	const {id, go, users, dispatch} = props;
	const osname = platform();

	useFetchedFriends(dispatch);

	return(
		<Panel id={id}>
			<PanelHeader
				left={<HeaderButton onClick={go} data-to="home">
					{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
				</HeaderButton>}
			>
				Друзья
			</PanelHeader>
			
			{users && users.map(user => (
				<User fetchedUser={{
					id: user.id,
					first_name: user.first_name,
					last_name: user.last_name
				}} go={go} />
			))}

		</Panel>
	);
}

FriendsContainer.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired
};

const mapStateToProps = (state) => {
	const {
		selectFriends: {getFriends}
	} = rootSelects;

	return {
		users: getFriends(state)
	};
};

export default connect(mapStateToProps, null)(FriendsContainer);
