import {combineReducers} from 'redux';
import user, * as selectUser from './user/reducer';
import accessToken, * as selectAccessToken from './accessToken/reducer';
import friends, * as selectFriends from './friends/reducer';

export default combineReducers({
	user,
	accessToken,
	friends
});

export {
	selectUser,
	selectAccessToken,
	selectFriends
}