import { SET_USER } from './actionType';

const userInitialState = {
	photo_200: '',
	first_name: 'Вася',
	last_name: 'Васечкин',
	city: { title: 'New York' }
};

export default function user (state = userInitialState, action = {}) {
	switch (action.type) {
		case SET_USER:
			return {
				...state,
				...action.payload
			};
		default:
			return state;
	};
};

export const getUser = (state) => {
	return state.user;
};